<template>
  <section class="pc-container pb-4 bg-white">
    <div class="pcoded-content">
      <div class="row mb-4 d-flex justify-content-between align-items-center">
        <div class="col-sm-12 col-md-12 col-xl-12">
          <h5>Lista de Expedientes</h5>
        </div>
      </div>
      <div class="row p-0 m-0">
        <div class="col-xl-12 p-0 m-0">
          <div class="card p-0 m-0 border-0 shadow-none">
            <div class="card-header border-0 p-0 m-0 mb-4">
              <div class="row">
                  <div class="col-sm-12 col-md-6 col-xl-6">
                    <input type="text" class="form-control w-100" placeholder="Buscar" v-model="busqueda" style="border-radius: 8px;"/>
                  </div>
                  <div class="col-sm-12 col-md-6 col-xl-3">
                    <select class="form-select border-1 w-100" v-model.number="type" @change="filterData()"  style="border-radius: 8px;">
                      <option value="" selected>Tipo</option>
                      <option value="1">Civil / Laboral</option>
                      <option value="2">Penal</option>
                      <option value="2">Arbitral</option>
                      <option value="2">Indecopi</option>
                    </select>
                  </div>
                  <div class="col-sm-12 col-md-6 col-xl-3">
                    <select class="form-select border-1 w-100" v-model="status" @change="filterData()"  style="border-radius: 8px;">
                      <option value="" selected>Estado</option>
                      <option value="En Tramite">En Tramite</option>
                      <option value="En Ejecucion">En Ejecucion</option>
                    </select>
                  </div>
                </div>
            </div>
            <div class="card-body p-0 m-0">
              <div class="table-responsive p-0 m-0">
                <table class="table table-hover table-sm" aria-describedby="table-expedientes" id="table" ref="dataTable">
                  <thead>
                    <tr>
                      <th class="bg-white fw-bold text-dark border-0">#</th>
                      <th class="bg-white fw-bold text-dark border-0">N de Expediente</th>
                      <th class="bg-white fw-bold text-dark border-0">Fecha de Inicio</th>
                      <th class="bg-white fw-bold text-dark border-0">Parte Procesal</th>
                      <th class="bg-white fw-bold text-dark border-0">Materia</th>
                      <th class="bg-white fw-bold text-dark border-0">Pretensin/Delito</th>
                      <th class="bg-white fw-bold text-dark border-0">Fecha de Registro</th>
                      <th class="bg-white fw-bold text-dark border-0">Estado</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-if="expedientesFiltrados.length === 0">
                      <td colspan="8" class="text-secondary text-center">No se encontraron resultados...</td>
                    </tr>
                    <tr v-for="(expediente, index) in  expedientesPaginados" :key="index">
                      <td>{{ primerRegistro + index }}</td>
                      <td><a href="javascript:void(0)" @click="redirectDetalle(expediente.exp_id)">{{
                        expediente.numero }}</a></td>
                      <td>{{ $formatDate(expediente.fecha_inicio) }}</td>
                      <td>{{ getNombreCompleto(expediente.procesal, expediente.multiple) }}</td>
                      <td>{{ expediente.materia }}</td>
                      <td>{{ expediente.pretencion }}</td>
                      <td>{{ $formatDate(expediente.creacion) }}</td>
                      <td>
                        <span :class="badgeClass(expediente.estado_proceso)">
                          {{ expediente.estado_proceso }}
                        </span>
                        </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <nav aria-label="..." class="d-flex justify-content-between align-items-center">
                <div>
                  <span>Mostrando {{ expedientesPaginados.length === 0 ? "0" : primerRegistro }} a {{ ultimoRegistro }}
                    de {{ expedientesFiltrados.length }} registros</span>
                </div>
                <ul class="pagination">
                  <li class="page-item" :class="{ disabled: currentPage === 1 }">
                    <a class="page-link" href="javascript:void(0)" @click="prevPage">Anterior</a>
                  </li>
                  <li class="page-item" :class="{ disabled: currentPage === totalPages }">
                    <a class="page-link" href="javascript:void(0)" @click="nextPage">Siguiente</a>
                  </li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { badgeClass } from "@/utils/utils.js";
import axios from "@/plugins/axios.js";
import { debounce } from "lodash";

export default {
  name: "Buscar",
  data() {
    return {
      tipos: [],
      expedientes: [],
      temp: [],
      currentPage: 1,
      itemsPerPage: 25,
      busqueda: '',
      type: '',
      status: ''
    };
  },
  mounted() {
    this.cargartiposExpedientes();
    this.cargarExpedientes();
  },
  computed: {
    id() {
      return this.$store.getters.get__user.abo_id;
    },
    expedientesFiltrados() {

      return this.expedientes.filter((expediente) => {
        const busquedaLowerCase = this.busqueda.toLowerCase();
        return (
          expediente.numero.toLowerCase().includes(busquedaLowerCase) ||
          expediente.pretencion.toLowerCase().includes(busquedaLowerCase) ||
          expediente.materia.toLowerCase().includes(busquedaLowerCase) ||
          expediente.procesal.some((proceso) => {
            if (!proceso) return false;
            return (
              proceso.tipo_procesal.toLowerCase().includes(busquedaLowerCase) ||
              proceso.tipo_persona.toLowerCase().includes(busquedaLowerCase) ||
              (proceso.dni && proceso.dni.toString().toLowerCase().includes(busquedaLowerCase)) ||
              (proceso.apellido_paterno && proceso.apellido_paterno.toLowerCase().includes(busquedaLowerCase)) ||
              (proceso.apellido_materno && proceso.apellido_materno.toLowerCase().includes(busquedaLowerCase)) ||
              (proceso.nombres && proceso.nombres.toLowerCase().includes(busquedaLowerCase)) ||
              (proceso.apellido_paterno + ' ' + proceso.apellido_materno + ' ' + proceso.nombres).toLowerCase().includes(busquedaLowerCase) ||
              (proceso.apellido_paterno + ' ' + proceso.apellido_materno).toLowerCase().includes(busquedaLowerCase) ||
              (proceso.razon_social && proceso.razon_social.toString().toLowerCase().includes(busquedaLowerCase))
            );
          })
        );
      });
    },

    // Calcula el nmero total de pginas
    totalPages() {
      return Math.ceil(
        this.expedientesFiltrados.length / this.itemsPerPage,
      );
    },
    // Calcula el ndice del primer registro en la pgina actual
    primerRegistro() {
      return (this.currentPage - 1) * this.itemsPerPage + 1;
    },
    // Calcula el ndice del ltimo registro en la pgina actual
    ultimoRegistro() {
      const ultimo = this.currentPage * this.itemsPerPage;
      return ultimo > this.expedientesFiltrados.length
        ? this.expedientesFiltrados.length
        : ultimo;
    },
    // Devuelve los expedientes de la pgina actual basados en la paginacin
    expedientesPaginados() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.expedientesFiltrados.slice(startIndex, endIndex);
    },
  },

  methods: {

    filterData(){
      const { status, type, temp } = this;

      const filterByType = (expediente) => expediente.tipo_exp === type;
      const filterByStatus = (expediente) => expediente.estado_proceso === status;

      if (status && type) {
        this.expedientes = temp.filter((expediente) => filterByType(expediente) && filterByStatus(expediente));
      } else if (type) {
        this.expedientes = temp.filter(filterByType);
      } else if (status) {
        this.expedientes = temp.filter(filterByStatus);
      } else {
        this.expedientes = temp;
      }
    },

    // Mtodo para cambiar a la pgina siguiente
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },

    // Mtodo para cambiar a la pgina anterior
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    async cargartiposExpedientes() {
      try {
        const response = await axios.get("proceedingTypes");
        this.tipos = response.data;
      } catch (error) {
        console.log(error);
      }
    },

    async cargarExpedientes() {
      try {
        this.$showLoadingAlert();

        let response = null;
        if (this.id === null) {
          response = await axios.get('proceeding', { abo_id: this.id });
        } else {
          response = await axios.post('lawyer/expedientes', { abo_id: this.id });
        }
        this.temp = response.data;
        this.expedientes = response.data;
        console.log(this.expedientes);
      } catch (error) {
        console.log(error);
      } finally {
        this.$closeLoadingAlert();
      }
    },

    redirectDetalle(id) {
      this.$router.push({ name: "expediente/detalle", params: { id } });
    },
    getNombreCompleto(procesal, multiple) {
      const tipoProcesal = procesal[0].tipo_procesal;
      return this.esDemandanteODemandado(tipoProcesal)
        ? this.obtenerNombreCompleto(procesal, multiple)
        : "UNPRG";
    },
    esDemandanteODemandado(tipoProcesal) {
      return (
        tipoProcesal === "DEMANDANTE" || tipoProcesal === "DEMANDADO"
      );
    },
    obtenerNombreCompleto(procesal, multiple) {
      if (multiple === "1") {
        return "Demanda Colectiva";
      }

      const {
        tipo_persona,
        apellido_paterno,
        apellido_materno,
        nombres,
        razon_social,
      } = procesal[0];
      return tipo_persona === "NATURAL"
        ? `${apellido_paterno} ${apellido_materno} ${nombres}`
        : razon_social;
    },
    badgeClass,
    formatFecha(fechaISO) {
      const fecha = new Date(fechaISO);
      const opcionesFormato = {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "numeric",
        minute: "numeric",
        second: "numeric",
      };
      return fecha.toLocaleDateString("es-ES", opcionesFormato);
    },
    formatDate(fechaISO) {
      const fecha = new Date(fechaISO);
      const opcionesFormato = {
        year: "numeric",
        month: "short",
        day: "numeric",
      };
      return fecha.toLocaleDateString("es-ES", opcionesFormato);
    },

    debounceSearch: debounce(function () {
      this.currentPage = 1; // Restablece la pgina actual al buscar
    }, 300),
  },
};
</script>

<style scoped>
@import "@/assets/css/fileStatus.css";
@import '@/assets/css/dataTable.css';


</style>
